@import url(https://fonts.googleapis.com/css?family=Gabriela|Lato:400,400i,700,700i&subset=cyrillic,cyrillic-ext,latin-ext);
* {
  margin: 0;
  padding: 0;
}

html{
  font-family: 'Lato', sans-serif;
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;   
}

.main-header{ 
  width: 100%;
  background-color: #337ab7;
}

.wrapper-header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.main-menu{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 20%;
}

.btn-menu{
  width: 5rem;
  height: 5rem;
  align-self: center;
  background-color: #337ab7;
  border: 1px solid #aaaaaa;
  border-radius: 5%;
  font-family: 'Gabriela', serif;
  font-weight: bold;
  font-size: 1.2rem;
  color: white;
  text-shadow: 3px 3px 5px black;
  box-shadow: 0 0 10px black;
}

.title-header {
  padding: 30px 10px;
  width: 80%;
  font-family: 'Gabriela', serif;
  font-size: 1.8rem;
  text-align: center;
  color: white;
  text-shadow: 3px 3px 5px black;
}

.list-menu{
  max-height: 0px;
  overflow: hidden;
  transition: max-height 0.8s ease;  
  width: 100%;  
  list-style: none; 
  display: block; 
}

.links-menu{
  display: block;
  width: 90%;
  padding: 10px 20px;
  margin: 10px auto;
  border: 1px solid #DDDDDD;
  border-radius: 5px;
  text-decoration: none;
  color: white; 
}

.list-menu > li > .links-menu{
  font-weight: bold;
}

.links-menu:hover, .links-menu:active{
  background-color: blue;
  color: yellow;
  cursor: pointer;
}

.active{
  max-height: 2000px;  
}

.bread-crumbs {
	padding: 10px 2rem;
	border-top: 1px solid lightgray;
	font-weight: 600;
	color: white;
}

.cursor-pointer {
	cursor: pointer;
}

.active-bread-crumbs {
	color: lightgray;
}

.footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.adress, .contact, .map{
  padding: 20px;
  font-family: 'Gabriela', serif;
  font-size: 1.1rem;
  color: white;
}

.map{
  border-right: none;
}

.map > iframe{
  display: block;
}

.center{
  text-align: center;
}

.text-adress{
  font-family: 'Lato', sans-serif;
}

.facebook {
  display: block;
  margin: 0 auto;
  height: 3rem;
  width: auto;
}

