@media (min-width: 1200px) {
	
	html{
  		font-size: 15px;
	}
	
	.container {
		width: 1020px;	
	}
	
	.list-section > li {
		width: 24%;
	}
	.footer {
		flex-direction: row;
	}
	.adress, .contact, .map{
		width: 33%;
		border-right: 1px solid white;
	}
	.map {
		border-right: none;
	}
	.wrapper-person {
		width: 60%;
	}
	.news-article {
		max-width: 60%;
	}
	.wrapper-allimg-news img {
		width: 70%;
		height: 70%;
		z-index: 0;
	}
	.wrapper-methodcabinet {
		width: 60%;
	}
}

